
import { Options, Vue } from "vue-class-component";
import ManualMove from "@/components/ManualMove.vue";
import ManualTools from "@/components/ManualTools.vue";
import { Plotter } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
  components: {
    ManualMove,
    ManualTools,
  },
})
export default class Home extends Vue {
  plotter!: Plotter;

  get validCoords(): boolean {
    return this.plotter.lastx > 0 && this.plotter.lasty > 0;
  }

  async sendHome(): Promise<void> {
    await this.plotter.sendHome();
  }
}
