
import { Options, Vue } from "vue-class-component";
import { Plotter } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class ManualHome extends Vue {
  plotter!: Plotter;

  x = 0.0;
  y = 0.0;

  async sendHome(): Promise<void> {
    await this.plotter.sendHome();
  }

  async sendManualHome(): Promise<void> {
    await this.plotter.sendManualHome(this.x, this.y);
  }
}
