
import { Options, Vue } from "vue-class-component";
import { Logger } from "@/lib/Logger";
import { GCodeStatus, Plotter } from "@/lib/Plotter";
import { GCodeParser } from "@/lib/GCodeParser";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class GCodeUpload extends Vue {
  plotter!: Plotter;

  file: File[] = [];

  get status(): string {
    switch (this.plotter.status) {
      case 0:
        return "Running";
      case 1:
        return "Paused";
      case 2:
        return "Stopped";
      default:
        return "Unknown";
    }
  }

  async send(): Promise<void> {
    if (this.file.length == 0) {
      Logger.error("No File Provided");
      return;
    }
    if (this.plotter.status == GCodeStatus.PAUSE) {
      this.plotter.status = GCodeStatus.PLAY;
      return;
    }
    Logger.info(`Loading Gcode File: ${this.file[0].name}`);
    const parser = new GCodeParser(this.file[0]);
    await parser.parse();
    this.plotter.status = GCodeStatus.PLAY;
    await this.plotter.sendGCode(parser);
  }

  async pause(): Promise<void> {
    if (this.plotter.status == GCodeStatus.PLAY) {
      this.plotter.status = GCodeStatus.PAUSE;
    }
  }
  async stop(): Promise<void> {
    this.plotter.status = GCodeStatus.STOP;
  }

  async check(): Promise<void> {
    if (this.file.length == 0) {
      Logger.error("No File Provided");
      return;
    }
    Logger.info(`Loading Gcode File: ${this.file[0].name}`);
    const parser = new GCodeParser(this.file[0]);
    try {
      await parser.parse();
      Logger.info("G Code Valid");
    } catch {
      Logger.info("G Code Invalid");
    }
  }
}
