
import { Options, Vue } from "vue-class-component";
import StepperMove from "@/components/StepperMove.vue";
import ManualHome from "@/components/ManualHome.vue";
import { Plotter } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
  components: {
    StepperMove,
    ManualHome,
  },
})
export default class Home extends Vue {
  plotter!: Plotter;

  async sendHome(): Promise<void> {
    await this.plotter.sendHome();
  }
}
