
import { Options, Vue } from "vue-class-component";
import { Plotter, PlotterToolSelect } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class LoggerDisplay extends Vue {
  plotter!: Plotter;

  options = [
    {
      label: "No Tool",
      value: PlotterToolSelect.NONE,
    },
    { label: "Pen", value: PlotterToolSelect.PEN },
    { label: "Eraser", value: PlotterToolSelect.ERASER },
  ];

  tool: PlotterToolSelect = PlotterToolSelect.NONE;

  async setTool(): Promise<void> {
    await this.plotter.sendToolSelect(this.tool);
  }
}
